<template>
  <main class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          Количество товаров {{ countProducts }}
        </span>
      </div>
    </div>

    <div class="content__catalog">

      <ProductFilter :filters="filters" @change-filters="setFilters"/>

      <section class="catalog">

        <div v-if="productsLoading">Загрузка товаров...</div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров
          <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>

        <ProductList :products="products" />

        <BasePagination v-model="page" :count="countProducts" :per-page="productPerPage" />

      </section>


    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_URL } from '../config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filters: {
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategoryId: 0,
        filterColorId: [],
        filterMaterialId: [],
        filterSeasonId: [],
      },

      page: 1,
      productPerPage: 12,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    }
  },

  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map(product => {
          return {
            ...product,
            images: product.colors.flatMap((color) => color.gallery).filter((img) => img !== null)
          }
        })
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    }
  },

  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(API_URL + `/products`, {
          params: {
            page: this.page,
            limit: this.productPerPage,
            minPrice: this.filters.filterPriceFrom,
            maxPrice: this.filters.filterPriceTo,
            categoryId: this.filters.filterCategoryId,
            materialIds: this.filters.filterMaterialId,
            seasonIds: this.filters.filterSeasonId,
            colorIds: this.filters.filterColorId
          },
        })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);
      }, 500);
    },
    setFilters(filters) {
      this.filters = filters;
    },
    setPage(page) {
      this.page = page;
    }
  },

  watch: {
    filters() {
      this.page = 1;
      this.loadProducts();
    },
    page() {
      this.loadProducts();
    },
    '$route'() {
      this.loadProducts();
    }
  },

  created() {
    this.filters.filterCategoryId = this.$route.query.categoryId || 0;
    this.loadProducts();
  }

}
</script>
