<template>
    <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          Количество товаров: {{ totalAmount }}
        </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">

            <CartItem v-for="item in products" :key="item.id" :title="item.product.title" :img="item.color.gallery[0].file.url" :basket-item-id="item.id" :item-id="item.product.id" :color-code="item.color.color.code" :color-title="item.color.color.title" :price="item.product.price" :amount="item.quantity"/>

          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link :to="{name: 'order'}" tag="button" class="cart__button button button--primery" type="submit"
          v-show="totalAmount > 0">
            Оформить заказ
          </router-link>

        </div>
      </form>
    </section>
  </main>
</template>

<script>
import CartItem from '@/components/CartItem.vue';
import numberFormat from '@/helpers/numberFormat';
// import { mapGetters } from 'vuex';

export default {
  filters: {
    numberFormat,
  },

  components: { CartItem },

  computed: {
    // ...mapGetters({ products: 'cartDetailProducts', totalsPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount' }),
    products() { //cart
      return this.$store.getters.cartDetailProducts;
    },
    totalPrice() {
      return this.$store.getters.cartTotalPrice;
    },
    totalAmount() {
      return this.$store.getters.cartTotalAmount;
    },
  },

}
</script>
