<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар"
    :disabled="productAmount === 1" @click.prevent="changeProductAmount(productAmount - 1)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" :value="productAmount" @input="$emit('input', $event.target.productAmount)">

    <button type="button" aria-label="Добавить один товар" @click.prevent="changeProductAmount(productAmount + 1)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  model: {
    prop: 'productAmount',
    event: 'changeProductAmount',
  },
  props: ['productAmount'],

  methods: {
    changeProductAmount(productAmount) {
      this.$emit('changeProductAmount', productAmount);
    },
  },
};
</script>
