<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="img" width="120" height="120" :alt="title">
    </div>
    <h3 class="product__title">
      {{ title }}
    </h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{ 'background-color': colorCode }"></i>
        {{ colorTitle }}
      </span>
    </p>
    <span class="product__code">
      Артикул: {{ itemId }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" :disabled="amount === 1" @click.prevent=" productAmount --">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="productAmount" name="count">

      <button type="button" aria-label="Добавить один товар" @click.prevent=" productAmount ++">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ price * productAmount }} ₽
    </b>

    <div class="product__status">
      <span class="product__error" v-if="isSendingFailed">Что-то пошло не так. Попробуйте позже!</span>
    </div>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
    @click.prevent="deleteItem">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import axios from 'axios';
import { API_URL } from '@/config.js';
// import { mapActions } from 'vuex';

export default {
  props: ['item', 'title', 'img', 'basketItemId', 'itemId', 'colorCode', 'colorTitle', 'price', 'amount'],
  data() {
    return {
      isSending: false,
      isSendingFailed: false,
    }
  },

//   computed: {
//     productAmount: {
//       get() {
//         return this.amount;
//       },
//       set(val) {
//         this.$store.dispatch('updateCartProductAmount', { productId: this.itemId, amount: val });
//       },
//     },
//   },

//   methods: {
//     ...mapActions(['deleteCartProducts']),
//     setItemAmount(val) {
//       this.productAmount = val;
//     },
//   },
// };
  computed: {
    productAmount: {
      get() {
        return this.amount;
      },
      async set(val) {
        this.isSendingFailed = false;
        this.$store.commit('updateCartProductAmount', { productId: this.itemId, amount: val });
        try {
          const { data } = await axios.put(`${API_URL}/baskets/products`,
            { basketItemId: this.basketItemId, amount: val },
            {
              params: { userAccessKey: this.$store.state.userAccessKey }
            });
          this.$store.commit('updateCartProductsData', data.items);
        } catch (err) {
          this.isSendingFailed = true;
          this.$store.commit('syncCartProducts');
        }
      }
    },
  },
  methods: {

    setItemAmount(val) {
      this.productAmount = val;
    },

    deleteItem() {
      this.$store.dispatch('deleteCartProducts', { basketItemId: this.basketItemId, productId: this.itemId });
    },
  }
}
</script>
