<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="sendOrder">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText v-model="formData.name" :error="formError.name" title="ФИО"
              placeholder="Введите ваше полное имя" />

            <BaseFormText v-model="formData.address" :error="formError.address" title="Адрес доставки"
              placeholder="Введите ваш адрес" />

            <BaseFormText v-model="formData.phone" :error="formError.phone" title="Телефон"
              placeholder="Введите ваш телефон" type="tel" />

            <BaseFormText v-model="formData.email" :error="formError.email" title="Email" placeholder="Введите ваш Email"
              type="email" />

            <BaseFormTextarea v-model="formData.comment" :error="formError.comment" title="Комментарий к заказу"
              placeholder="Ваши пожелания" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>

            <ul class="cart__options options">
              <li class="options__item" v-for="delivery in deliveries" :key="delivery.id">
                <label class="options__label" >
                  <input class="options__radio sr-only" type="radio" name="delivery" :value="delivery.id" v-model="formData.deliveryId">
                  <span class="options__value">
                    {{ delivery.title }} <b>{{ delivery.price }}</b>
                  </span>
                </label>
              </li>
            </ul>
            <span class="form__error" v-if="formError.deliveryTypeId">{{ formError.deliveryTypeId }}</span>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="payment in payments" :key="payment.id">
                <label class="options__label" >
                  <input class="options__radio sr-only" type="radio" name="pay" :value="payment.id" v-model="formData.paymentId">
                  <span class="options__value">
                    {{ payment.title }}
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.productId">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.productId }}</span>
            </li>

          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{ deliveryPrice ? `${deliveryPrice} ₽` : 'бесплатно' }}</b></p>
            <p>Итого: <b>{{ totalAmount }}</b> товара на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>

        <div v-if="orderLoading">Оформление заказа...
          <div id="escapingBallG">
            <div id="escapingBall_1" class="escapingBallG"></div>
          </div>
        </div>

        <div class="cart__error form__error-block" v-if="formError">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ orderErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
// import axios from 'axios';
// import { API_URL } from '@/config';

export default {
  components: { BaseFormText, BaseFormTextarea },
  data() {
    return {
      formData: {
        name: null,
        address: null,
        phone: null,
        email: null,
        comments: null,
        paymentId: 1,
        deliveryId: 1,
      },
      // formError: {},
      // formErrorMessage: '',

      orderLoading: false,
      orderLoadingFaled: false,
    };
  },

  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount' }),
    payments() {
      return this.$store.state.paymentsData;
    },
    deliveries() {
      return this.$store.state.deliveriesData;
    },
    deliveryPrice() {
      return this.deliveries ? +this.deliveries.find(delivery => delivery.id === this.formData.deliveryId).price : 0;
    },
    formError() {
      return this.$store.state.orderErrorInfo || {};
    },
    formErrorMessage() {
      return this.$store.state.formErrorMessage;
    },
    orderErrorMessage() {
      return this.$store.state.orderErrorMessage;
    },
    isSending() {
      return this.$store.state.isSending;
    },
    isSendingFailed() {
      return this.$store.state.isSendingFailed;
    },
    isLoading() {
      return this.$store.state.isLoading;
    },
    isLoadingFailed() {
      return this.$store.state.isLoadingFailed;
    },

  },

  filters: {
    numberFormat,
  },

  methods: {

    sendOrder() {
      this.$store.dispatch('sendOrder', {
        name: this.formData.name,
        address: this.formData.address,
        phone: this.formData.phone,
        email: this.formData.email,
        deliveryTypeId: this.formData.deliveryId,
        paymentTypeId: this.formData.paymentId,
        comments: this.formData.comments
      });
    },
    loadPayments() {
      this.$store.dispatch('loadPayments', { deliveryTypeId: this.formData.deliveryId });
    }
  },

  watch: {
    'formData.deliveryId'() {
      this.loadPayments();
      this.formData.paymentId = null;
    }
  },

  created() {
    this.$store.dispatch('loadDeliveries');
    this.loadPayments();
  }
}
</script>


<style scoped>
#escapingBallG {
  position: relative;
  width: 125px;
  height: 43px;
  margin: auto;
}

.escapingBallG {
  background-color: rgb(0, 0, 0);
  position: absolute;
  top: 0;
  left: 0;
  width: 43px;
  height: 43px;
  border-radius: 21px;
  -o-border-radius: 21px;
  -ms-border-radius: 21px;
  -webkit-border-radius: 21px;
  -moz-border-radius: 21px;
  animation-name: bounce_escapingBallG;
  -o-animation-name: bounce_escapingBallG;
  -ms-animation-name: bounce_escapingBallG;
  -webkit-animation-name: bounce_escapingBallG;
  -moz-animation-name: bounce_escapingBallG;
  animation-duration: 1.5s;
  -o-animation-duration: 1.5s;
  -ms-animation-duration: 1.5s;
  -webkit-animation-duration: 1.5s;
  -moz-animation-duration: 1.5s;
  animation-iteration-count: infinite;
  -o-animation-iteration-count: infinite;
  -ms-animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-timing-function: linear;
  -o-animation-timing-function: linear;
  -ms-animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
  -moz-animation-timing-function: linear;
  animation-delay: 0s;
  -o-animation-delay: 0s;
  -ms-animation-delay: 0s;
  -webkit-animation-delay: 0s;
  -moz-animation-delay: 0s;
  transform: scale(0.5, 1);
  -o-transform: scale(0.5, 1);
  -ms-transform: scale(0.5, 1);
  -webkit-transform: scale(0.5, 1);
  -moz-transform: scale(0.5, 1);
}



@keyframes bounce_escapingBallG {
  0% {
    left: 0px;
    transform: scale(0.5, 1);
  }

  25% {
    left: 41px;
    transform: scale(1, 0.5);
  }

  50% {
    left: 103px;
    transform: scale(0.5, 1);
  }

  75% {
    left: 41px;
    transform: scale(1, 0.5);
  }

  100% {
    left: 0px;
    transform: scale(0.5, 1);
  }
}

@-o-keyframes bounce_escapingBallG {
  0% {
    left: 0px;
    -o-transform: scale(0.5, 1);
  }

  25% {
    left: 41px;
    -o-transform: scale(1, 0.5);
  }

  50% {
    left: 103px;
    -o-transform: scale(0.5, 1);
  }

  75% {
    left: 41px;
    -o-transform: scale(1, 0.5);
  }

  100% {
    left: 0px;
    -o-transform: scale(0.5, 1);
  }
}

@-ms-keyframes bounce_escapingBallG {
  0% {
    left: 0px;
    -ms-transform: scale(0.5, 1);
  }

  25% {
    left: 41px;
    -ms-transform: scale(1, 0.5);
  }

  50% {
    left: 103px;
    -ms-transform: scale(0.5, 1);
  }

  75% {
    left: 41px;
    -ms-transform: scale(1, 0.5);
  }

  100% {
    left: 0px;
    -ms-transform: scale(0.5, 1);
  }
}

@-webkit-keyframes bounce_escapingBallG {
  0% {
    left: 0px;
    -webkit-transform: scale(0.5, 1);
  }

  25% {
    left: 41px;
    -webkit-transform: scale(1, 0.5);
  }

  50% {
    left: 103px;
    -webkit-transform: scale(0.5, 1);
  }

  75% {
    left: 41px;
    -webkit-transform: scale(1, 0.5);
  }

  100% {
    left: 0px;
    -webkit-transform: scale(0.5, 1);
  }
}

@-moz-keyframes bounce_escapingBallG {
  0% {
    left: 0px;
    -moz-transform: scale(0.5, 1);
  }

  25% {
    left: 41px;
    -moz-transform: scale(1, 0.5);
  }

  50% {
    left: 103px;
    -moz-transform: scale(0.5, 1);
  }

  75% {
    left: 41px;
    -moz-transform: scale(1, 0.5);
  }

  100% {
    left: 0px;
    -moz-transform: scale(0.5, 1);
  }
}
</style>
