<template>
  <aside class="filter">
    <form class="filter__form form" action="#" method="get" @submit.prevent="setFilters">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model="currentCategoryId">
            <option value="0">Все категории</option>
            <option :value="category.id" v-for="category in categories" :key="category.id">{{ category.title }}</option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors colors--black">
          <li class="colors__item" v-for="color in colors" :key="color.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="checkbox" name="color" :value="color.id"
                v-model="currentColorId">
              <span class="colors__value" :style="{ 'background-color': color.code }">
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="material in materials" :key="material.id">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="material" :value="material.id"
                v-model="currentMaterialId">
              <span class="check-list__desc">
                {{ material.title }}
                <span>({{ material.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="season in seasons" :key="season.id">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="collection" :value="season.id"
                v-model="currentSeasonId">
              <span class="check-list__desc">
                {{ season.title }}
                <span>({{ season.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click="resetFilters">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import axios from 'axios';
import { API_URL } from '../config';

export default {
  emits: ['change-filters'],
  props: ['filters'],
  data() {
    return {
      currentPriceFrom: this.filters.filterPriceFrom,
      currentPriceTo: this.filters.filterPriceTo,
      currentCategoryId: this.filters.filterCategoryId,
      currentColorId: this.filters.filterColorId,
      currentMaterialId: this.filters.filterMaterialId,
      currentSeasonId: this.filters.filterSeasonId,

      categoriesData: null,
      colorsData: null,
      materialsData: null,
      seasonsData: null,
    };
  },

  computed: {
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
    materials() {
      return this.materialsData ? this.materialsData.items : [];
    },
    seasons() {
      return this.seasonsData ? this.seasonsData.items : [];
    },
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    productFilters() {
      return {
        filterPriceFrom: this.currentPriceFrom,
        filterPriceTo: this.currentPriceTo,
        filterCategoryId: this.currentCategoryId,
        filterColorId: this.currentColorId,
        filterMaterialId: this.currentMaterialId,
        filterSeasonId: this.currentSeasonId
      }
    },

  },

  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
  },

  methods: {
    loadColors() {
      axios.get(`${API_URL}/colors`)
      .then(response => this.colorsData = response.data);
    },

    loadCategories() {
      axios.get(`${API_URL}/productCategories`)
      .then(response => this.categoriesData = response.data);
    },

    loadMaterials() {
      axios.get(`${API_URL}/materials`)
      .then(response => this.materialsData = response.data);
    },

    loadSeasons() {
      axios.get(`${API_URL}/seasons`)
      .then(response => this.seasonsData = response.data);
    },

    setFilters() {
      this.$emit('change-filters', this.productFilters);
    },
    resetFilters() {
      this.currentPriceFrom = 0;
      this.currentPriceTo = 0;
      this.currentCategoryId = 0;
      this.currentColorId = [];
      this.currentMaterialId = [];
      this.currentSeasonId = [];
      this.$router.replace({ 'query': null });
      this.setFilters();
    },
  },

  created() {
    this.loadColors();
    this.loadCategories();
    this.loadMaterials();
    this.loadSeasons();
  },
}
</script>
